import React from "react";
import { useQuery } from "@tanstack/react-query";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Loader2, Bug, Phone, User } from "lucide-react";
import axiosInstance from "@/api/axios/axiosInstance";
import usePermissions from "@/hooks/usePermissions";

const PhoneNumberDebug = () => {
  const { isSuperAdmin } = usePermissions();

  // Debug: Get all assignments
  const { data: allAssignments, isLoading: assignmentsLoading, refetch: refetchAssignments } = useQuery({
    queryKey: ["debug-assignments"],
    queryFn: async () => {
      const response = await axiosInstance.get("/api/phone-numbers/debug/assignments");
      return response.data;
    },
    retry: 1,
  });

  // Debug: Get user's phone numbers
  const { data: userNumbers, isLoading: userLoading, refetch: refetchUser } = useQuery({
    queryKey: ["debug-user-numbers"],
    queryFn: async () => {
      const response = await axiosInstance.get("/api/phone-numbers/debug/user-numbers");
      return response.data;
    },
    retry: 1,
  });

  // Regular phone numbers API
  const { data: regularNumbers, isLoading: regularLoading, refetch: refetchRegular } = useQuery({
    queryKey: ["regular-phone-numbers"],
    queryFn: async () => {
      const response = await axiosInstance.get("/api/phone-numbers");
      return response.data;
    },
    retry: 1,
  });

  const handleRefreshAll = () => {
    refetchAssignments();
    refetchUser();
    refetchRegular();
  };

  if (assignmentsLoading || userLoading || regularLoading) {
    return (
      <Card>
        <CardContent className="flex items-center justify-center h-32">
          <Loader2 className="h-8 w-8 animate-spin" />
        </CardContent>
      </Card>
    );
  }

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-2">
              <Bug className="h-5 w-5 text-orange-600" />
              <CardTitle>Phone Number Assignment Debug</CardTitle>
            </div>
            <Button onClick={handleRefreshAll} variant="outline" size="sm">
              Refresh All
            </Button>
          </div>
          <CardDescription>
            Debug information for phone number assignments. This helps identify why assigned phone numbers might not be showing up for users.
          </CardDescription>
        </CardHeader>
      </Card>

      {/* User Debug Info */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <User className="h-4 w-4" />
            Current User Debug Info
          </CardTitle>
        </CardHeader>
        <CardContent>
          {userNumbers && (
            <div className="space-y-4">
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <strong>User ID:</strong> {userNumbers.debug_info?.user_id}
                </div>
                <div>
                  <strong>Is Super Admin:</strong> {userNumbers.debug_info?.is_super_admin ? "Yes" : "No"}
                </div>
                <div>
                  <strong>Phone Count:</strong> {userNumbers.debug_info?.phone_count}
                </div>
                <div>
                  <strong>Is Super Admin (Hook):</strong> {isSuperAdmin ? "Yes" : "No"}
                </div>
              </div>
              
              <div>
                <strong>Phone Number IDs:</strong>
                <div className="mt-2">
                  {userNumbers.debug_info?.phone_number_ids?.length > 0 ? (
                    userNumbers.debug_info.phone_number_ids.map((id: string) => (
                      <Badge key={id} variant="outline" className="mr-2 mb-2">
                        {id}
                      </Badge>
                    ))
                  ) : (
                    <Badge variant="destructive">No phone number IDs found</Badge>
                  )}
                </div>
              </div>

              <div>
                <strong>User's Phone Numbers:</strong>
                {userNumbers.data?.length > 0 ? (
                  <Table className="mt-2">
                    <TableHeader>
                      <TableRow>
                        <TableHead>DB ID</TableHead>
                        <TableHead>Phone Number ID</TableHead>
                        <TableHead>Number</TableHead>
                        <TableHead>User ID</TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {userNumbers.data.map((phone: any) => (
                        <TableRow key={phone.id}>
                          <TableCell>{phone.id}</TableCell>
                          <TableCell>{phone.phone_number_id || "N/A"}</TableCell>
                          <TableCell>{phone.number || "N/A"}</TableCell>
                          <TableCell>{phone.user_id}</TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                ) : (
                  <Badge variant="destructive" className="mt-2">No phone numbers found for user</Badge>
                )}
              </div>
            </div>
          )}
        </CardContent>
      </Card>

      {/* All Assignments (Super Admin Only) */}
      {isSuperAdmin && (
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Phone className="h-4 w-4" />
              All Phone Number Assignments
            </CardTitle>
          </CardHeader>
          <CardContent>
            {allAssignments && (
              <div className="space-y-4">
                <div className="grid grid-cols-3 gap-4">
                  <div>
                    <strong>Total:</strong> {allAssignments.debug_info?.total_count}
                  </div>
                  <div>
                    <strong>Assigned:</strong> {allAssignments.debug_info?.assigned_count}
                  </div>
                  <div>
                    <strong>Unassigned:</strong> {allAssignments.debug_info?.unassigned_count}
                  </div>
                </div>

                {allAssignments.data?.length > 0 ? (
                  <Table>
                    <TableHeader>
                      <TableRow>
                        <TableHead>DB ID</TableHead>
                        <TableHead>Phone Number ID</TableHead>
                        <TableHead>Number</TableHead>
                        <TableHead>Assigned User</TableHead>
                        <TableHead>User ID</TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {allAssignments.data.map((phone: any) => (
                        <TableRow key={phone.id}>
                          <TableCell>{phone.id}</TableCell>
                          <TableCell>{phone.phone_number_id || "N/A"}</TableCell>
                          <TableCell>{phone.number || "N/A"}</TableCell>
                          <TableCell>
                            {phone.user_name ? (
                              <div>
                                <div className="font-medium">{phone.user_name}</div>
                                <div className="text-xs text-muted-foreground">{phone.user_email}</div>
                              </div>
                            ) : (
                              <Badge variant="outline">Unassigned</Badge>
                            )}
                          </TableCell>
                          <TableCell>{phone.user_id || "NULL"}</TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                ) : (
                  <Badge variant="destructive">No phone numbers found in database</Badge>
                )}
              </div>
            )}
          </CardContent>
        </Card>
      )}

      {/* Regular API Response */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Phone className="h-4 w-4" />
            Regular Phone Numbers API Response
          </CardTitle>
        </CardHeader>
        <CardContent>
          {regularNumbers && (
            <div className="space-y-4">
              <div>
                <strong>Success:</strong> {regularNumbers.success ? "Yes" : "No"}
              </div>
              <div>
                <strong>Count:</strong> {regularNumbers.data?.length || 0}
              </div>
              
              {regularNumbers.data?.length > 0 ? (
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>ID</TableHead>
                      <TableHead>Number</TableHead>
                      <TableHead>Status</TableHead>
                      <TableHead>Created By</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {regularNumbers.data.map((phone: any) => (
                      <TableRow key={phone.id}>
                        <TableCell>{phone.id}</TableCell>
                        <TableCell>{phone.number || "N/A"}</TableCell>
                        <TableCell>{phone.status || "N/A"}</TableCell>
                        <TableCell>{phone.created_by || phone.user_name || "N/A"}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              ) : (
                <Badge variant="destructive">No phone numbers returned by regular API</Badge>
              )}
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
};

export default PhoneNumberDebug;
