import React, { useState } from "react";
import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Loader2, Phone, UserPlus, UserMinus, Users, Shield } from "lucide-react";
import { toast } from "sonner";
import axiosInstance from "@/api/axios/axiosInstance";

import {
  getAllPhoneNumbersForAdmin,
  getAllVapiPhoneNumbersForAdmin,
  assignPhoneNumberToUser,
  unassignPhoneNumber
} from "@/api/services/phoneNumbers/phoneNumberService";
import { getAllUsersWithRoles } from "@/api/services/user/userService";
import type { PhoneNumber } from "@/api/services/phoneNumbers/phoneNumberService";

interface AdminUser {
  id: number;
  username?: string;
  email: string;
  first_name?: string;
  last_name?: string;
  is_active: boolean;
}

const SuperAdminPhoneNumberManagement = () => {
  const [selectedPhoneNumber, setSelectedPhoneNumber] = useState<PhoneNumber | null>(null);
  const [selectedUserId, setSelectedUserId] = useState<string>("");
  const [isAssignDialogOpen, setIsAssignDialogOpen] = useState(false);

  const queryClient = useQueryClient();

  // Fetch all VAPI phone numbers with assignment info for admin
  const { data: phoneNumbersData, isLoading: numbersLoading, error: numbersError } = useQuery({
    queryKey: ["admin-vapi-phone-numbers"],
    queryFn: () => getAllVapiPhoneNumbersForAdmin(),
    retry: 1,
  });

  // Fetch all users for assignment
  const { data: usersData, isLoading: usersLoading } = useQuery({
    queryKey: ["all-users"],
    queryFn: () => getAllUsersWithRoles(),
    retry: 1,
  });

  // Assignment mutation
  const assignMutation = useMutation({
    mutationFn: ({ phoneNumberId, userId, vapiId }: { phoneNumberId: string; userId: string; vapiId?: string }) => {
      // For VAPI phone numbers, send both the VAPI ID and user ID
      if (vapiId) {
        return axiosInstance.post(`/api/phone-numbers/${vapiId}/assign`, {
          user_id: userId,
          vapi_id: vapiId
        });
      } else {
        // Traditional assignment for existing local records
        return assignPhoneNumberToUser(phoneNumberId, userId);
      }
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["admin-vapi-phone-numbers"] });
      queryClient.invalidateQueries({ queryKey: ["admin-phone-numbers"] });
      toast.success("Phone number assigned successfully!");
      setIsAssignDialogOpen(false);
      setSelectedPhoneNumber(null);
      setSelectedUserId("");
    },
    onError: (error: any) => {
      toast.error("Failed to assign phone number: " + (error.response?.data?.message || error.message));
    },
  });

  // Unassignment mutation
  const unassignMutation = useMutation({
    mutationFn: (phoneNumberId: string) => unassignPhoneNumber(phoneNumberId),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["admin-vapi-phone-numbers"] });
      queryClient.invalidateQueries({ queryKey: ["admin-phone-numbers"] });
      toast.success("Phone number unassigned successfully!");
    },
    onError: (error: any) => {
      toast.error("Failed to unassign phone number: " + (error.response?.data?.message || error.message));
    },
  });

  const handleAssignClick = (phoneNumber: PhoneNumber) => {
    setSelectedPhoneNumber(phoneNumber);
    setIsAssignDialogOpen(true);
  };

  const handleAssignSubmit = () => {
    if (!selectedPhoneNumber || !selectedUserId) {
      toast.error("Please select a user to assign the phone number to.");
      return;
    }

    assignMutation.mutate({
      phoneNumberId: selectedPhoneNumber.local_id || selectedPhoneNumber.id,
      userId: selectedUserId,
      vapiId: selectedPhoneNumber.vapi_id || selectedPhoneNumber.id // Use VAPI ID for assignment
    });
  };

  const handleUnassign = (phoneNumber: PhoneNumber) => {
    if (!phoneNumber.local_id) {
      toast.error("Cannot unassign phone number that is not in local database.");
      return;
    }

    if (window.confirm(`Are you sure you want to unassign phone number ${phoneNumber.number}?`)) {
      unassignMutation.mutate(phoneNumber.local_id);
    }
  };

  const getStatusBadge = (status: string) => {
    switch (status) {
      case "active":
        return <Badge variant="default" className="bg-green-100 text-green-800">Active</Badge>;
      case "inactive":
        return <Badge variant="secondary">Inactive</Badge>;
      case "pending":
        return <Badge variant="outline">Pending</Badge>;
      default:
        return <Badge variant="secondary">Unknown</Badge>;
    }
  };

  const phoneNumbers = phoneNumbersData?.data || [];
  const users = usersData?.data || [];

  if (numbersLoading) {
    return (
      <Card>
        <CardContent className="flex items-center justify-center h-32">
          <Loader2 className="h-8 w-8 animate-spin" />
        </CardContent>
      </Card>
    );
  }

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <div className="flex items-center gap-2">
            <Shield className="h-5 w-5 text-blue-600" />
            <CardTitle>Super Admin - Phone Number Management</CardTitle>
          </div>
          <CardDescription>
            Manage phone number assignments for all users. Shows all VAPI phone numbers including Twilio numbers.
            Assign any available phone number to specific users to control access.
          </CardDescription>
          {phoneNumbersData?.debug_info && (
            <div className="mt-4 p-3 bg-blue-50 rounded-lg">
              <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-sm">
                <div>
                  <span className="font-medium text-blue-900">Total VAPI Numbers:</span>
                  <div className="text-blue-700">{phoneNumbersData.debug_info.total_vapi_numbers}</div>
                </div>
                <div>
                  <span className="font-medium text-green-900">Assigned:</span>
                  <div className="text-green-700">{phoneNumbersData.debug_info.assigned_numbers}</div>
                </div>
                <div>
                  <span className="font-medium text-orange-900">Available:</span>
                  <div className="text-orange-700">{phoneNumbersData.debug_info.unassigned_numbers}</div>
                </div>
                <div>
                  <span className="font-medium text-purple-900">In Local DB:</span>
                  <div className="text-purple-700">{phoneNumbersData.debug_info.in_local_db}</div>
                </div>
              </div>
            </div>
          )}
        </CardHeader>
        <CardContent>
          {phoneNumbers.length === 0 ? (
            <div className="text-center py-8">
              <Phone className="h-12 w-12 text-muted-foreground mx-auto mb-4" />
              <h3 className="text-lg font-medium mb-2">No phone numbers found</h3>
              <p className="text-muted-foreground">
                No phone numbers are available for assignment.
              </p>
            </div>
          ) : (
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Phone Number</TableHead>
                  <TableHead>Provider</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead>Assigned User</TableHead>
                  <TableHead>Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {phoneNumbers.map((phoneNumber: PhoneNumber) => (
                  <TableRow key={phoneNumber.id}>
                    <TableCell className="font-mono">{phoneNumber.number || "Unknown"}</TableCell>
                    <TableCell>
                      <Badge variant="outline" className={phoneNumber.provider === 'vapi' ? 'bg-blue-50 text-blue-700' : 'bg-gray-50 text-gray-700'}>
                        {phoneNumber.provider?.toUpperCase() || 'VAPI'}
                      </Badge>
                    </TableCell>
                    <TableCell>
                      <div className="flex gap-2">
                        {phoneNumber.is_assigned ? (
                          <Badge variant="default" className="bg-green-100 text-green-800">Assigned</Badge>
                        ) : (
                          <Badge variant="outline" className="text-orange-600">Available</Badge>
                        )}
                        {phoneNumber.is_in_local_db ? (
                          <Badge variant="secondary" className="text-xs">In DB</Badge>
                        ) : (
                          <Badge variant="outline" className="text-xs text-blue-600">VAPI Only</Badge>
                        )}
                      </div>
                    </TableCell>
                    <TableCell>
                      {phoneNumber.user_name ? (
                        <div className="flex flex-col">
                          <span className="font-medium">{phoneNumber.user_name}</span>
                          <span className="text-xs text-muted-foreground">{phoneNumber.user_email}</span>
                        </div>
                      ) : (
                        <Badge variant="outline" className="text-orange-600">Unassigned</Badge>
                      )}
                    </TableCell>
                    <TableCell>
                      <div className="flex gap-2">
                        {phoneNumber.is_assigned ? (
                          <Button
                            size="sm"
                            variant="outline"
                            onClick={() => handleUnassign(phoneNumber)}
                            disabled={unassignMutation.isPending || !phoneNumber.is_in_local_db}
                            title={!phoneNumber.is_in_local_db ? "Cannot unassign phone number not in local database" : ""}
                          >
                            <UserMinus className="h-3 w-3 mr-1" />
                            Unassign
                          </Button>
                        ) : (
                          <Button
                            size="sm"
                            variant="outline"
                            onClick={() => handleAssignClick(phoneNumber)}
                            disabled={assignMutation.isPending}
                          >
                            <UserPlus className="h-3 w-3 mr-1" />
                            Assign
                          </Button>
                        )}
                      </div>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          )}
        </CardContent>
      </Card>

      {/* Assignment Dialog */}
      <Dialog open={isAssignDialogOpen} onOpenChange={setIsAssignDialogOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Assign Phone Number</DialogTitle>
            <DialogDescription>
              Assign phone number {selectedPhoneNumber?.number} to a user. Only the assigned user will be able to see and use this phone number.
            </DialogDescription>
          </DialogHeader>
          <div className="space-y-4">
            <div>
              <label className="text-sm font-medium">Select User</label>
              <Select value={selectedUserId} onValueChange={setSelectedUserId}>
                <SelectTrigger>
                  <SelectValue placeholder="Choose a user to assign the phone number to" />
                </SelectTrigger>
                <SelectContent>
                  {users.map((user: any) => (
                    <SelectItem key={user.id} value={user.id.toString()}>
                      <div className="flex items-center gap-2">
                        <Users className="h-4 w-4" />
                        <div>
                          <div className="font-medium">{user.first_name || ''} {user.last_name || ''}</div>
                          <div className="text-xs text-muted-foreground">{user.email}</div>
                        </div>
                      </div>
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
            <div className="flex justify-end gap-2">
              <Button variant="outline" onClick={() => setIsAssignDialogOpen(false)}>
                Cancel
              </Button>
              <Button 
                onClick={handleAssignSubmit}
                disabled={!selectedUserId || assignMutation.isPending}
              >
                {assignMutation.isPending ? (
                  <>
                    <Loader2 className="h-4 w-4 mr-2 animate-spin" />
                    Assigning...
                  </>
                ) : (
                  <>
                    <UserPlus className="h-4 w-4 mr-2" />
                    Assign Phone Number
                  </>
                )}
              </Button>
            </div>
          </div>
        </DialogContent>
      </Dialog>
    </div>
  );
};

export default SuperAdminPhoneNumberManagement;
